<template>
  <skeleton v-if="skeletonLoading" :nums="4"></skeleton>
  <div v-else class="container">
    <div class="item dark" @click="toEcard">
      <div class="item-top">
        <div class="left">
          <span class="logo ecard"></span>
          <span class="title">智家e卡</span>
        </div>
        <div class="right">
          <span class="fee">{{walletInfo.ecardBalance?walletInfo.ecardBalance.toFixed(2):'0.00'}}<span class="yuan">元</span></span>
          <span class="right-arrow"></span>
        </div>
      </div>
      <div class="item-bottom">
        <span>可用卡:<span class="number"> {{walletInfo.ecardNumber}}</span> 张</span>
      </div>
    </div>
    <!-- 钻石 -->
    <div class="item light" @click="toDiamond">
      <div class="item-top">
        <div class="left">
          <span class="logo diamond"></span>
          <span class="title">钻石</span>
        </div>
        <div class="right">
          <span class="fee"> {{walletInfo.d}}<span class="yuan">钻</span></span>
          <span class="right-arrow"></span>
        </div>
      </div>
      <div class="item-bottom">
        <span class="total">累计消费:<span class="number"> {{Number(walletInfo.consumDiamond).toFixed(2)}}</span> 元</span>
        <span>已抵现:<span class="number"> {{Number(walletInfo.consumDiamondPoint).toFixed(2)}}</span> 元</span>
      </div>
    </div>
  </div>
</template>

<script>
import { getWallet } from '@/http/service/mine'
import skeleton from '@/components/skeleton'

export default {
  components: {
    skeleton
  },
  name: 'wallet',
  computed: {

  },
  activated() {

  },
  created() {
    if (this.$util.isHaierApp()) { // 智家
      this.$store.dispatch('handleUpAuth').then((res) => {
        if (res) {
          this.initData()
        } else {
          this.$store.dispatch('doLogin')
        }
      });
    } else {
      this.initData()
    }
  },
  data() {
    return {
      skeletonLoading: true,
      walletInfo: null, // 钱包信息
    }
  },
  methods: {
    initData() {
      getWallet().then(res => {
        if (res.data.success) {
          this.walletInfo = res.data.data
          console.log('this.walletInfo', this.walletInfo)
          this.skeletonLoading = false
        }
      })
    },
    toEcard() {
      this.$router.push('/ecardlist')
    },
    toDiamond() {
      this.$router.push('/diamond')
    }
  }
}
</script>

